import React from 'react';
import { Link } from 'gatsby';
import Img from 'gatsby-image';
import check from '../../img/check.png';
import classes from './CarouselItem.module.scss';

// Components
import ProductBuy from '../ProductBuy/ProductBuy';
import ProductPrice from '../ProductPrice/ProductPrice';

function CarouselItem({ img, title, price, link }) {
	return (
		<Link to={`/product/${title}`}>
			<div className={`ml-2 mr-2 pb-3 pt-3 ${classes.cont}`}>
				<div className="d-flex justify-content-center">
					<Img alt="img" style={{ width: '100px' }} fluid={img} />
				</div>
				<h5 className={classes.titletxt}>{title}</h5>
				<p style={{ color: '#9e9e9e' }} className="text-right">
					<img alt="img" className="mr-2 pl-2" src={check} style={{ width: '23px', display: 'inline' }} />
					گارانتی اصالت و سلامت فیزیکی کالا
				</p>
				<ProductPrice price={price} />
				<ProductBuy link={link} />
			</div>
		</Link>
	);
}

export default CarouselItem;
