import React, { Fragment } from 'react';
import Media from 'react-media';
import Slider from 'react-slick';
import CarouselItem from '../CarouselItem/CarouselItem';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

function CarouselItems({ latestproducts }) {
	const settings = {
		dots: false,
		infinite: true,
		speed: 500,
		slidesToShow: 4,
		slidesToScroll: 1
	};
	const settingssmall = {
		dots: false,
		infinite: true,
		speed: 500,
		slidesToShow: 1,
		slidesToScroll: 1
	};
	return (
		<Media
			queries={{
				small: '(max-width: 850px)',
				large: '(min-width: 850px)'
			}}
		>
			{(matches) => (
				<Fragment>
					{matches.small && (
						<Slider className="mt-3  mr-3 ml-3" {...settingssmall}>
							{latestproducts.edges.map((each) => {
								const title = each.node.title.content[0].content[0].value;
								return (
									<CarouselItem
										title={title}
										img={each.node.img[0].fluid}
										link={each.node.link.content[0].content[0].value}
										price={each.node.price}
										key={each.node.price}
									/>
								);
							})}
						</Slider>
					)}

					{matches.large && (
						<Slider className="mt-3" {...settings}>
							{latestproducts.edges.map((each) => {
								const title = each.node.title.content[0].content[0].value;
								return (
									<CarouselItem
										title={title}
										img={each.node.img[0].fluid}
										link={each.node.link.content[0].content[0].value}
										price={each.node.price}
										key={each.node.price}
									/>
								);
							})}
						</Slider>
					)}
				</Fragment>
			)}
		</Media>
	);
}

export default CarouselItems;
