import React from 'react';
import classes from './Showcase.module.scss';
import Img from 'gatsby-image';
import { Link } from 'gatsby';

function Showcase({ monitor, ps4Blue, ps4controller, pubg, xbox }) {
	return (
		<React.Fragment>
			<div className="row mt-3">
				<div className="col-6">
					<div className={`ml-1 mr-1 mb-2 ${classes.workbox}`}>
						<Link to="/product/ایکس باکس Xbox One X">
							<Img alt="img" className={classes.imges} fluid={xbox} />
						</Link>
					</div>
				</div>
				<div className="col-6">
					<div className={`ml-1 mr-1 mb-2 ${classes.workbox}`}>
						<Link to="/product/پی اس چهار PS4 Slim">
							<Img alt="img" className={classes.imges} fluid={ps4Blue} />
						</Link>
					</div>
				</div>
			</div>
			<div className="row mt-3">
				<div className="col-lg-4 col-md-4 col-12">
					<div className={`ml-1 mr-1 mb-2 ${classes.workbox}`}>
						<Link to="/product/مانیتور 24 اینچ">
							<Img alt="img" className={classes.imges} fluid={monitor} />
						</Link>
					</div>
				</div>
				<div className="col-lg-4 col-md-4 col-12">
					<div className={`workbox ml-1 mr-1 mb-2 ${classes.workbox}`}>
						<Link to="/product/دسته بازی Pubg">
							<Img alt="img" className={classes.imges} fluid={pubg} />
						</Link>
					</div>
				</div>
				<div className="col-lg-4 col-md-4 col-12">
					<div className={`ml-1 mr-1 mb-2 ${classes.workbox}`}>
						<Link to="/product/دسته بازی Ps4 اصل">
							<Img alt="img" className={classes.imges} fluid={ps4controller} />
						</Link>
					</div>
				</div>
			</div>
		</React.Fragment>
	);
}

export default Showcase;
