import React from 'react';
import { Link } from 'gatsby';
import Card from '../Card/Card';
import leftarw from '../../img/leftarw.png';

function Latest({ lat, mainText, secondText, secondLink, cls }) {
	return (
		<div className={`pb-3 ${cls}`} style={{ background: 'white', borderRadius: '7px' }}>
			<p style={{ color: '#9e9e9e' }} className="text-right m-0 p-0 pb-2 mr-3 pt-2">
				{mainText}
			</p>
			<hr className="m-0" style={{ background: 'white' }} />
			<div dir="rtl" className="row pb-3">
				{lat.map((each) => {
					return (
						<div className="col-lg-6 col-md-6 col-sm-12 col-12">
							<Card
								key={each.node.link}
								title={each.node.title.content[0].content[0].value}
								img={each.node.img.fluid}
								desc={each.node.desc.content[0].content[0].value}
								date={each.node.date.content[0].content[0].value}
								link={each.node.link}
							/>
						</div>
					);
				})}
			</div>
			<Link className="ml-3" to={secondLink}>
				<img alt="arrow" style={{ width: '14px' }} src={leftarw} />
				{secondText}
			</Link>
		</div>
	);
}

export default Latest;
