import React from 'react';
import { graphql } from 'gatsby';

// Components
import Layout from '../components/Layout/Layout';
import Showcase from '../components/Showcase/Showcase';
import Latest from '../components/Latest/Latest';
import CarouselItems from '../components/CarouselItems/CarouselItems';

export const query = graphql`
	{
		ps4Blue: file(relativePath: { eq: "img/ps4blue.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		ps4controller: file(relativePath: { eq: "img/ps4controller.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		xbox: file(relativePath: { eq: "img/xbox.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		pubg: file(relativePath: { eq: "img/pubg.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		latestproducts: allContentfulProduct(
			sort: { fields: updatedAt, order: DESC }
			limit: 8
			filter: { category: { eq: "items" } }
		) {
			edges {
				node {
					title {
						content {
							content {
								value
							}
						}
					}
					desc {
						content {
							content {
								value
							}
						}
					}
					link {
						content {
							content {
								value
							}
						}
					}
					category
					price
					link {
						content {
							content {
								value
							}
						}
					}
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
				}
			}
		}
		monitor: file(relativePath: { eq: "img/monitor.jpg" }) {
			childImageSharp {
				fluid {
					...GatsbyImageSharpFluid
				}
			}
		}
		xboxItems: allContentfulBlog(limit: 2, filter: { category: { eq: "xbox" } }) {
			edges {
				node {
					date {
						content {
							content {
								value
							}
						}
					}
					title {
						content {
							content {
								value
							}
						}
					}
					desc {
						content {
							content {
								value
							}
						}
					}
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
					link
				}
			}
		}
		latest: allContentfulBlog(limit: 2, sort: { fields: updatedAt, order: DESC }) {
			edges {
				node {
					date {
						content {
							content {
								value
							}
						}
					}
					title {
						content {
							content {
								value
							}
						}
					}
					desc {
						content {
							content {
								value
							}
						}
					}
					img {
						fluid {
							...GatsbyContentfulFluid
						}
					}
					link
				}
			}
		}
	}
`;

export default function Home({ data }) {
	return (
		<Layout title="صفحه اصلی">
			<div className="container">
				<Showcase
					xbox={data.xbox.childImageSharp.fluid}
					monitor={data.monitor.childImageSharp.fluid}
					pubg={data.pubg.childImageSharp.fluid}
					ps4Blue={data.ps4Blue.childImageSharp.fluid}
					ps4controller={data.ps4controller.childImageSharp.fluid}
				/>
				<div className="mt-4">
					<Latest mainText="آخرین مطالب" secondText="همه مطالب" secondLink="/blog" lat={data.latest.edges} />
					<CarouselItems latestproducts={data.latestproducts} />
					<br />
					<Latest
						mainText="ایکس باکس"
						secondText="مطالب ایکس باکس"
						secondLink="/blog/xbox"
						lat={data.xboxItems.edges}
						cls={'mb-5'}
					/>
				</div>
			</div>
		</Layout>
	);
}
