import React from 'react';
import classes from './Card.module.scss';
import dateimg from '../../img/date.png';
import Img from 'gatsby-image';
import { Link } from 'gatsby';

function Card({ img, title, desc, date }) {
	return (
		<Link to={`/blog/${title}`}>
			<div className={classes.cont}>
				<h5 className={`m-0 p-0 mb-3 pt-3 ${classes.txt}`}>{title}</h5>
				<div dir="rtl" className="d-flex">
					<Img alt="img" style={{ borderRadius: '7px' }} fluid={img} />

					<div dir="rtl" className="mr-3 w-50 h-50">
						<p className={classes.text}>{desc}</p>

						<p style={{ color: 'black' }} className="m-0 p-0 mt-3 text-right">
							<img className="pl-2" alt="dateimg" src={dateimg} style={{ width: '24px' }} /> {date}
						</p>
					</div>
				</div>
			</div>
			<hr className="mb-0" />
		</Link>
	);
}

export default Card;
